import React from 'react';
import { Link } from 'react-router-dom';
import { ConfirmationButton } from '../Infrastructure';
import { post, get } from '../../services/fetch';

export class UserEditComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      errors: {}
    };
  }

  componentDidMount() {
    this.getData();
  }

  getData() {
    get(`/user-edit/${this.props.match.params.id}`)
      .then(e => this.setState(e));
  }

  update() {
    post(`/user-edit/${this.state.id}`, this.state)
      .then(e => {
        this.props.onChange(e);
        this.props.history.push('/users');
      })
      .catch(err => this.setState({ errors: err }));
  }

  render() {
    return (
      <div className="form-container">
        <h2 className="form-box">  </h2>
        <form className="form-box">
          <div className="input-group">
            <div>Email</div>
            <input
              type="email"
              value={this.state.email}
              onChange={e => this.setState({ email: e.target.value })} />
            <div className="error">{this.state.errors.email ? '* ' : null}</div>
          </div>

          <div className="input-group">
            <div></div>
            <input
              type="text"
              value={this.state.name}
              onChange={e => this.setState({ name: e.target.value })} />
            <div className="error">{this.state.errors.name ? '* ' : null}</div>
          </div>

          <div className="input-group">
            <div>  </div>
            <input type="date"
              value={this.state.birthDate}
              onChange={e => this.setState({ birthDate: e.target.value })} />
            <div className="error">{this.state.errors.birthDate ? '* ' : null}</div>
          </div>

          <div className="input-group">
            <div> </div>
            <input
              value={this.state.telephone}
              type="text"
              onChange={e => this.setState({ telephone: e.target.value })} />
            <div className="error">{this.state.errors.telephone ? '* ' : null}</div>
          </div>

          <div className="input-group">
            <div></div>
            <select value={this.state.gender} onChange={e => this.setState({ gender: e.target.value })} >
              <option disabled selected >--</option>
              <option value="male"></option>
              <option value="female"></option>
            </select>
            <div className="error">{this.state.errors.gender ? '* ' : null}</div>
          </div>

          <ConfirmationButton className="button-block center"
            onChange={flag => flag ? this.update() : null} >
            
          </ConfirmationButton>
        </form>
      </div>
    );
  }
}


