import React, { Component } from 'react';
import { post } from '../services/fetch';
import { ActionButton } from '../admin/Infrastructure';

export class RegistrationComponent extends Component {
  constructor(props) {
    super(props);

    this.state = {
      errors: {}
    };
  }

  create() {
    if (this.state.emailAgreement)
      return post('/users', this.state)
        .catch(err => {
          this.setState({ errors: err });
          throw err;
        });
    else return Promise.reject();
  }

  render() {
    return (
      <div className="form-container">
        <h2 className="form-box"></h2>
        <form className="form-box">
          <div className="input-group">
            <div>E-</div>
            <input
              type="email"
              onChange={e => this.setState({ email: e.target.value })} />
            <div className="error">{this.state.errors.email ? '* ' : null}</div>
          </div>
          <div className="input-group">
            <div></div>
            <input
              type="password"
              onChange={e => this.setState({ password: e.target.value })} />
            <div className="error">{this.state.errors.password ? '* ' : null}</div>
          </div>
          <div className="input-group">
            <div> </div>
            <input
              type="password"
              onChange={e => this.setState({ confirmPassword: e.target.value })} />
          </div>

          <div className="input-group">
            <div></div>
            <input
              type="text"
              onChange={e => this.setState({ name: e.target.value })} />
            <div className="error">{this.state.errors.name ? '* ' : null}</div>
          </div>

          <div className="input-group">
            <div>  </div>
            <input type="date"
              onChange={e => this.setState({ birthDate: e.target.value })} />
            <div className="error">{this.state.errors.birthDate ? '* ' : null}</div>
          </div>

          <div className="input-group">
            <div> </div>
            <input
              type="text"
              onChange={e => this.setState({ telephone: e.target.value })} />
            <div className="error">{this.state.errors.telephone ? '* ' : null}</div>
          </div>

          <div className="input-group">
            <div></div>
            <select onChange={e => this.setState({ gender: e.target.value })} >
              <option disabled selected >--</option>
              <option value="male"></option>
              <option value="female"></option>
            </select>
            <div className="error">{this.state.errors.gender ? '* ' : null}</div>
          </div>

          <div className="input-group">
            <div>
              <label className="cursor">
                <input type="checkbox"
                  onChange={e => this.setState({ emailAgreement: !this.state.emailAgreement })} />
                            </label>
            </div>
          </div>

          <ActionButton className="center"
            onSuccess="/login"
            disabled={!this.state.emailAgreement}
            onClick={() => this.create()}> </ActionButton>
        </form>
      </div>
    )
  }
}


